@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
	--primary-color: #d26a47;
	--secondary-color: #254fd8;
	--body-light-color: #292a2d;
	--body-dark-color: #202124;
	--body-darker-color: #1f1f1f;
}

* {
	font-family: 'Montserrat', sans-serif;
	box-sizing: border-box;
}

.font-family {
	font-family: 'Montserrat', sans-serif;
}

.container,
.container-fluid,
.container-xxl,
.container-xl,
.container-lg,
.container-md,
.container-sm {
	--bs-gutter-x: 1.5rem;
	--bs-gutter-y: 0;
	width: 100%;
	padding-right: calc(var(--bs-gutter-x) * 0.5);
	padding-left: calc(var(--bs-gutter-x) * 0.5);
	margin-right: auto;
	margin-left: auto;
}

@media (min-width: 576px) {

	.container-sm,
	.container {
		max-width: 540px;
	}
}

@media (min-width: 768px) {

	.container-md,
	.container-sm,
	.container {
		max-width: 720px;
	}
}

@media (min-width: 992px) {

	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 960px;
	}
}

@media (min-width: 1200px) {

	.container-xl,
	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 1140px;
	}
}

@media (min-width: 1400px) {

	.container-xxl,
	.container-xl,
	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 1320px;
	}
}

.primary-color {
	background-color: var(--primary-color);
	color: #fff;
}

.secondary-color {
	background-color: var(--secondary-color);
	color: #fff;
}

.body-light-color {
	background-color: var(--body-light-color);
}

.body-dark-color {
	background-color: var(--body-dark-color);
}

.body-darker-color {
	background-color: var(--body-darker-color);
}

.text-color {
	color: var(--primary-color);
}

.font-30 {
	font-size: 30px;
}

.center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.end {
	display: flex;
	justify-content: end;
	align-items: center;
}

